<template>
  <div class="classList mt72 bf bx">
    <div class="myBigBox">
      <div class="myBoxleft">
        <div class="littleBox">
          <img src="../../../static/images/学习指导.png" alt />
          <span>学习指导:</span>
        </div>
        <div class="littleBox">
          <img src="../../../static/images/学习目标.png" alt />
          <span>学习目标:</span>
        </div>
        <div class="littleBox">
          <img src="../../../static/images/重点难点.png" alt />
          <span>重点难点:</span>
        </div>
        <div class="littleBox">
          <img src="../../../static/images/方法指导.png" alt />
          <span>方法指导:</span>
        </div>
        <!-- <div class="resourceTest">
          <span class="test">资源评价</span>
          <el-button size="small" class="myBtn">优</el-button>
          <el-button size="small" class="myBtn">良</el-button>
          <el-button size="small" class="myBtn">中</el-button>
          <el-button size="small" class="myBtn">差</el-button>
        </div>-->
      </div>
      <div class="myBoxRight">
        <div class="myResource">
          <img
            src="https://img.zcool.cn/community/019f5f5df06cf1a801219cceb72bde.jpg@1280w_1l_2o_100sh.jpg"
            alt
          />
        </div>
        <div class="myResourceText">
          <p>
            习近平在会上发表重要讲话，总结2019年经济工作，分析当前经济形势，部署2020年经济工作。李克强在讲话中对明年经济工作作出具体部署，并作了总结讲话。
            会议认为，今年以来，面对国内外风险挑战明显上升的复杂局面，在以习近平同志为核心的党中央坚强领导下，全党全国贯彻党中央决策部署，坚持稳中求进工作总基调，
            坚持以供给侧结构性改革为主线，推动高质量发展，扎实做好“六稳”工作，保持经济社会持续健康发展，三大攻坚战取得关键进展，精准脱贫成效显著，金融风险有效防控，
            生态环境质量总体改善，改革开放迈出重要步伐，供给侧结构性改革继续深化，科技创新取得新突破，人民群众获得感、幸福感、安全感提升，“十三五”规划主要指标进度符合预期
            ，全面建成小康社会取得新的重大进展。
          </p>
        </div>
      </div>
    </div>
    <div class="tearchOpinion">
      <p>
        <span>反馈意见</span>
      </p>
      <div class="OpinionList">
        <div class="OpinionImg">
          <img src="../../../static/images/反馈意见.png" alt />
        </div>
        <div class="OponionBox">
          <vue-baberrage
            :isShow="barrageIsShow"
            :barrageList="barrageList"
            :maxWordCount="maxWordCount"
            :throttleGap="throttleGap"
            :loop="barrageLoop"
            :boxHeight="boxHeight"
            :messageHeight="messageHeight"
          ></vue-baberrage>
        </div>
        <div class="OponionText">
          <quill-editor style="height:200px;width:100%"></quill-editor>
          <el-button class="sure">提交反馈</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { MESSAGE_TYPE } from "vue-baberrage";
export default {
  name: "ResourceDetail",

  data() {
    return {
      msg: "这次资源很好",
      barrageIsShow: true,
      messageHeight: 3,
      boxHeight: 50,
      barrageLoop: true,
      maxWordCount: 3,
      throttleGap: 5000,
      barrageList: []
    };
  },
  mounted() {
    this.addToList();
  },
  methods: {
    addToList() {
      let list = [
        {
          id: 1,
          avatar:
            "https://img.zcool.cn/community/018f645df820b9a801219cce64289e.jpg@1280w_1l_2o_100sh.jpg",
          msg: this.msg,
          time: 10,
          barrageStyle: "yellow"
        }
      ];
      list.forEach(v => {
        this.barrageList.push({
          id: v.id,
          avatar: v.avatar,
          msg: v.msg,
          time: v.time,
          type: MESSAGE_TYPE.NORMAL,
          barrageStyle: v.barrageStyle
        });
      });
    },
    toResource() {
      this.$router.push("/studentCommon/studyResource");
    }
  }
};
</script>
<style lang="less" scoped>
.mt72 {
  margin-top: 10px;
  background-color: #fff;
  .myBigBox {
    display: flex;

    .myBoxleft {
      display: inline-block;
      border-top: 10px solid #2bb7b3;
      width: 280px;
      padding: 0px 10px;
      .littleBox {
        height: 93px;
        width: 100%;
        border-bottom: 1px solid #eee;
        line-height: 93px;
        img {
          margin-left: 20px;
        }
      }
      .resourceTest {
        height: 170px;
        text-align: center;
        //  line-height: 170px;
        padding-top: 50px;
        .test {
          color: #0f8b84;
          font-size: 18px;
          display: block;
        }
        .myBtn {
          margin-top: 15px;
          border: 0;
          background-color: #ff8a00;
          color: #fff;
        }
      }
    }
    .myBoxRight {
      border-left: 10px solid #eee;
      display: inline-block;
      flex: 1;
      padding: 20px;
      .myResource {
        width: 100%;
        height: 375px;
        margin-bottom: 35px;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .tearchOpinion {
    p {
      display: block;
      text-align: center;
      height: 44px;
      background-color: #2bb7b3;
      border-bottom: 10px solid #eee;
      span {
        line-height: 44px;
        font-size: 20px;
        color: #fff;
      }
    }
    .OpinionList {
      .OpinionImg {
        img {
          margin-top: 20px;
        }
      }
    }
    .OponionBox {
      height: 100px;
    }
    .OponionText {
      padding: 30px 20px;
      position: relative;
      border-top: 1px solid #eee;
      height: 320px;
      .sure {
        position: absolute;
        right: 50%;
        bottom: 30px;
        background-color: #ff8b00;
        color: #fff;
        border: 0;
      }
    }
  }
}
</style>



